<template>
<div class="ft-simpleHead-container">
    <div class="ft-simpleHead-content">
        <ul v-for="menuItems in simpleMenus">
            <div class="ft-simpleHead-headMenu-container">
                <div class="ft-simpleHead-headMenu">
                    <i :class="'icon iconfont '+menuItems.icon"></i>
                </div>
                <div class="ft-simpleHead-name">
                    {{menuItems.name}}
                </div>
            </div>
            
            <div class="ft-simpleHead-menus">
                <li v-for="menuItem in menuItems.items">
                    <div class="ft-simpleHead-menus" :title="menuItem.name" @click="gotoMenu(menuItems.code,menuItem)">
                        <i :class="'icon iconfont '+menuItem.icon"></i>
                    </div>
                </li>
            </div>
            
        </ul>
    </div>
</div>
</template>
<script>
import {mapState,mapMutations} from 'vuex';

export default {
    data(){
        return{

        }
    },
    methods:{
        ...mapMutations(['getSimpleMenus']),
        gotoMenu:function(pCode,item){
            if(item.code=='suggest'){
                this.$parent.$refs.suggest.suggest()
            }else if(item.code=='reward'){

            }else{
                this.getSimpleMenus(pCode,item.code);
                this.$router.push({path:item.url});
            }
        }
    },
    computed:{
        ...mapState(['menu','simpleMenus'])
    }
}
</script>
<style lang="less" scoped>
@base-height:10px;
@head-collapse-height:16px;
@head-expand-height:300px;
@a-hundred-percent:100%;
@head-bg-color:rgba(0,0,0,0.6);

.ft-simpleHead-container{
    position: absolute;
    left: 0;
    top: 0;
    width: @a-hundred-percent;
    height: @head-collapse-height;
    background: @head-bg-color;
    overflow: hidden;
    z-index: 900; 
    transition: all 0.4s;
    -ms-transition:all 0.4s;
    -moz-transition:all 0.4s;
    -o-transition:all 0.4s;
    -webkit-transition:all 0.4s;
    .ft-simpleHead-content{
        position: relative;
        width: calc(@a-hundred-percent - @base-height*4);
        height: calc(@a-hundred-percent - @base-height*4);
        margin: 20px 20px;
        ul{
            position: relative;
            list-style: none;
            width: @a-hundred-percent;
            height: @a-hundred-percent*0.5;
            .ft-simpleHead-headMenu-container{
                position: relative;
                width: @base-height*4.6;
                height: @a-hundred-percent*0.5;
                float: left;
                .ft-simpleHead-headMenu{
                    position: relative;
                    width: @base-height*4.6;
                    height: @base-height*4.6;
                    border-radius: 50%;
                    background: #2B2B2B;
                    i{
                        position:absolute;
                        left: 6px;
                        top: -1px;
                        margin:0 auto;
                        font-size: 34px;
                        color:rgba(255,149,0,0.9);
                    }
                }
                .ft-simpleHead-name{
                    position: relative;
                    width: @base-height*4;
                    height: 13px;
                    margin: 5px 0;
                    color: rgba(255,149,0,0.9);
                    font-weight:bolder;
                    text-align: center;
                    overflow: hidden;        
                }
            }
            &:first-child{
                border-bottom: 1px dashed rgba(255,255,255,0.6);
            }
            
            .ft-simpleHead-menus{
                position: relative;
                float: left;
                width: calc(@a-hundred-percent - @base-height*8);
                height: @a-hundred-percent;
                transition: all 0.4s;
                -ms-transition:all 0.4s;
                -moz-transition:all 0.4s;
                -o-transition:all 0.4s;
                -webkit-transition:all 0.4s;
                li{
                    position: relative;
                    float: left;
                    width: @base-height*3;
                    height: @base-height*3;
                    margin: 0 @base-height;
                    cursor: pointer;
                    .ft-simpleHead-menus{
                        position: relative;
                        width: @base-height*4;
                        height: @base-height*4;
                        border-radius: 50%;
                        background: #2B2B2B;
                        i{
                            position:absolute;
                            left: 5px;
                            top: 0;
                            margin:0 auto;
                            font-size: 30px;
                            color:rgba(255,180,74,0.8);
                        }
                    }
                }
                :first-child{
                    margin-left:20px; 
                }
                :hover .ft-simpleHead-menus{
                    box-shadow:  0px 0px 10px #fff;
                    width: @base-height*4.4;
                    height: @base-height*4.4;
                }
                :hover .ft-simpleHead-menus>i{
                    color:#ff9500;
                    font-size: 32px;
                }
                
            }
        }
        
    }
}
&:hover {
    position: absolute;
    left: 0;
    top: 0;
    height: @head-expand-height;
}

</style>



