<template>
    <div class="ft-xmlFormat-container">
        <SimpleHead></SimpleHead>
        <div class="ft-xmlFormat-title">
            <div class="alert alert-warning alert-dismissible" role="alert" v-if="false">
            </div>
        </div>
        <div class="ft-xmlFormat-input">
            <textarea  class="form-control" v-model="inputVal" placeholder="待格式化xml字符串">{{inputVal}}</textarea>
        </div>
        <div class="ft-xmlFormat-btn">
            <button class="btn btn-default" type="submit" @click="format">格式化</button>
        </div>
        <div class="ft-xmlFormat-result">
            <textarea  class="form-control" v-model="resultVal" placeholder="格式化成功xml字符串" readonly>{{resultVal}}</textarea>
        </div>
        <div class="alert alert-danger alert-dismissible" role="alert" v-if="showTitle?true:false">
            <a href="#" class="close"  @click="hideTitle">
                &times;
            </a>
                <strong>提示：</strong>
                {{msg}}
        </div>
        <Suggest ref="suggest"></Suggest>
    </div>
</template>
<script>
import SimpleHead from '@/components/SimpleHead';
import Suggest from '@/components/head/Suggest';

export default {
    data(){
        return{
            inputVal:'',
            resultVal:'',
            showTitle:false,
            msg:''
        }
    },
    components:{
        SimpleHead:SimpleHead,
        Suggest:Suggest
    },
    methods:{
        hideTitle:function(){
            this.msg='';
            this.showTitle=false;
        },
        format:function(){
            var that=this;
            that.msg='';
            if(that.inputVal==''){
                that.msg="请输入待格式化xml字符串！";
                that.showTitle=true;
            }else{
                that.hideTitle();
                var params =  this.$Qs.stringify({
                    inputVal:that.inputVal,
                });
                this.$Axios.post('/FormatService/xml',params).then((res)=>{
                    var data=res.data;
                    if(!!data.success){
                        that.msg=data.errMsg;
                        that.showTitle=true;
                        that.resultVal=data.resultVal;
                    }else{
                        that.resultVal=data.resultVal;
                        that.msg=data.errMsg;
                        that.showTitle=true;
                    }
                });
            }
        }
    }
    
}
</script>
<style lang="less" scoped>
@a-hundred-percent:100%;
@head-height:0px;
@base-height:10px;


.ft-xmlFormat-container{
    width: @a-hundred-percent;
    height: @a-hundred-percent;
    text-align: center;
    font-size: 20px;
    .ft-xmlFormat-title{
        width: @a-hundred-percent;
        height: @a-hundred-percent*0.1;
        text-align: center;
        overflow: hidden;
        .alert-warning{
            font-size: 16px;
            font-weight: bold;
            color: #ff9500;
            span{
                font-size: @base-height*3;
            }
        }
    }
    .ft-xmlFormat-input{
        width: @a-hundred-percent*0.6;
        height: calc((@a-hundred-percent*0.9 - @base-height*5) / 2);
        margin-left: @a-hundred-percent*0.2;
        .form-control{
            height: @a-hundred-percent;
            border:1px solid #ccc;
            box-shadow:none;
            font-size: 16px;
        }
        .form-control:focus{
            border: 1px solid #9D9D9D;
            box-shadow:0 0 5px #ccc;
            background: #FFF8D7;
        }

    }
    .ft-xmlFormat-btn{
        width: @a-hundred-percent*0.6;
        height: @base-height*4;
        line-height: @base-height*4;
        margin-left: @a-hundred-percent*0.2;
        text-align: left;
        button{
            box-shadow:none;
            outline: none;
            background: #fff;
            font-size: 16px;
        }
        &:hover button{
            background: #FFF8D7;
        }
    }
    .ft-xmlFormat-result{
        width: @a-hundred-percent*0.6;
        height: calc((@a-hundred-percent*0.9 - @base-height*5) / 2);
        margin-left: @a-hundred-percent*0.2;
        .form-control{
            height: @a-hundred-percent*0.9;
            border:1px solid #ccc;
            box-shadow:none;
            font-size: 16px;
            background: #FFF8D7;
        }
    }
    .alert-danger{
        position: absolute;
        top:calc(50% - 40px);
        left: calc(50% - 150px);
        width: 300px;
        height: 80px;
        z-index: 200;
    }
}

</style>



