import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const state={
    menu:{},
    menus:[{
        id:'1',
        name:'Http工具',
        code:'http',
        items:[{
            id:'101',
            name:'http模拟测试',
            code:'http1',
            icon:'',
            url:'/'
        }]
    }
    ,{
        id:'2',
        name:'格式化工具',
        code:'format',
        items:[{
            id:'201',
            name:'json格式化',
            code:'json1',
            icon:'',
            url:'json'
        },{
            id:'202',
            name:'xml格式化',
            code:'xml',
            icon:'',
            url:'xml'
        }]
    },{
        id:'3',
        name:'实用工具',
        code:'compare',
        items:[{
            id:'301',
            name:'文件比较工具',
            code:'compare',
            icon:'',
            url:'compare'
        }]
    }],
    simpleMenus:[{
        id:'1',
        name:'工具',
        code:'tools',
        icon:'icon-ziyuanguanli',
        items:[{
            id:'101',
            name:'http模拟测试',
            code:'http1',
            icon:'icon-tool-https',
            url:'/'
        },{
            id:'102',
            name:'json格式化',
            code:'json1',
            icon:'icon-json',
            url:'json'
        },{
            id:'103',
            name:'xml格式化',
            code:'xml',
            icon:'icon-xml',
            url:'xml'
        },{
            id:'104',
            name:'文件比较工具',
            code:'compare',
            icon:'icon-duibi',
            url:'compare'
        }]
    },{
        id:'2',
        name:'系统',
        code:'menus',
        icon:'icon-shezhi',
        items:[{
            id:'201',
            name:'咨询与建议',
            code:'suggest',
            icon:'icon-jianyi',
            url:''
        }/*,{
            id:'202',
            name:'打赏',
            code:'reward',
            icon:'icon-dashang',
            url:''
        }*/]  
    }]

}

const mutations={
    getMenus(state,pCode,code){
        for(var i=0;i<state.menus.length;i++){
            var childMenus=state.menus[i];
            if(childMenus.code==pCode){
                for(var j=0;j<childMenus.items.length;j++){
                    var item=childMenus.items[j];
                    if(item.code==code){
                        state.menu=item;
                        break;
                    }
                }
            }
        }
    },
    getSimpleMenus(state,pCode,code){
        for(var i=0;i<state.simpleMenus.length;i++){
            var childMenus=state.simpleMenus[i];
            if(childMenus.code==pCode){
                for(var j=0;j<childMenus.items.length;j++){
                    var item=childMenus.items[j];
                    if(item.code==code){
                        state.menu=item;
                        break;
                    }
                }
            }
        }
    }
}

export default new Vuex.Store({
    state,
    mutations
})


