<template>
    <div class="ft-suggest-container">
        <div class="ft-suggest-icon" @click="suggest">
            <!--<i class="icon iconfont icon-jianyi" title="咨询与建议" ></i>-->
        </div>
        <div :class="mask?'ft-suggest-win':'win-hide'" >
            <div class="ft-suggest-win-head">
                咨询与建议
            </div>
            <div class="ft-suggest-win-content">
                <div class="ft-suggest-win-content-container">
                    <div class="ft-suggest-win-content-title">
                        <div class="alert alert-warning" role="alert" v-show="false"></div>
                    </div>
                    <div class="input-group">
                        <span class="input-group-addon" id="basic-addon1">邮箱地址:</span>
                        <input type="text" class="form-control" v-model="email" placeholder="请输入邮箱地址" aria-describedby="basic-addon2">
                    </div>
                    <textarea  class="form-control" v-model="remark" placeholder="咨询与建议"></textarea>
                </div>
                <div class="ft-suggest-win-btn">
                    <button type="button" class="btn btn-default" @click="commit">提交</button>
                    <button type="button" class="btn btn-default" @click="closeWin">关闭</button>
                </div>
            </div>
        </div>
        <div class="ft-suggest-mask" v-show="mask"></div>
        <div class="alert alert-danger alert-dismissible" role="alert" v-if="showTitle?true:false">
            <a href="#" class="close"  @click="hideTitle">
                &times;
            </a>
                <strong>提示：</strong>
                {{msg}}
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            mask:false,
            email:'',
            remark:'',
            showTitle:false,
            msg:'',
            rmClass:''
        }
    },
    methods:{
        suggest:function(){
            this.mask=true;
            this.hideTitle();
            this.removeClass();
        },
        closeWin:function(){
            this.mask=false;
        },
        hideTitle:function(){
            this.msg='';
            this.showTitle=false;
        },
        removeClass:function(){
            $(".ft-suggest-win").removeClass("win-rock");
            clearInterval(this.rmClass);
        },
        commit:function(){
            var that=this;
            var re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/; 
            if (!re.test(that.email)) {
                that.msg="请输入正确的邮箱地址！";
                that.showTitle=true;
                $(".ft-suggest-win").addClass("win-rock");
                that.rmClass=setInterval(that.removeClass,400);
            }else if(that.remark.trim().length<=0){
                that.msg="请输入咨询与建议！";
                that.showTitle=true;
                $(".ft-suggest-win").addClass("win-rock");
            }else{
                that.hideTitle();
                    var params =  this.$Qs.stringify({
                    email:that.email,
                    remark:that.remark
                });
                this.$Axios.post('/CustomService/suggest',params).then((res)=>{
                    var data=res.data;
                    if(!!data.success){
                        that.msg=data.errMsg;
                        that.showTitle=true;
                    }else{
                        that.msg=data.errMsg;
                        that.showTitle=true;
                    }
                });
            }
        }
    }
    
}
</script>
<style lang="less" scoped>
@base-height:10px;
@head-height:0px;
@span-width:120px;
@a-hundred-percent:100%;
@font-color:#ffb44a;
@font-hover-color:#ff9500;
@head-bg-color:rgba(0,0,0,0.8);
@head-hover-bg-color:rgba(0,0,0,0.2);
@auto-background-color:#FFF8D7;

.ft-suggest-container{
    width: @head-height;
    height: @head-height;
    .ft-suggest-icon{
        width: @head-height;
        height: @head-height;
        line-height: @head-height;
        text-align: center;
        cursor: pointer;
        .icon-jianyi{
            color: @font-color;
            font-size: 34px;
        }
        &:hover .icon-jianyi{
            color: @font-hover-color;
            font-size: 36px;
        }
    }
    .ft-suggest-win{
        position: absolute;
        left: @a-hundred-percent*0.3;
        top: @a-hundred-percent*0.2;
        width: @a-hundred-percent*0.4;
        height: @a-hundred-percent*0.6;
        background: #fff;
        border-radius: 5px;
        z-index: 1000;
        text-align: center;
        transition: all 0.4s;
        -ms-transition:all 0.4s;
        -moz-transition:all 0.4s;
        -o-transition:all 0.4s;
        -webkit-transition:all 0.4s;
        .ft-suggest-win-head{
            width: @a-hundred-percent;
            height: @base-height*5 - 1px;
            line-height:  @base-height*5 - 1px;
            color: @font-hover-color;
            font-size: 16px;
            font-weight:bolder;
            background: @auto-background-color;
            border-top-left-radius: 5px;
            border-top-right-radius: 5px;
            border-bottom:1px solid #ccc;
        }
        .ft-suggest-win-content{
            width: @a-hundred-percent;
            height: calc(@a-hundred-percent - @base-height*5);
            border-bottom-left-radius: 5px;
            border-bottom-right-radius: 5px;
            .ft-suggest-win-content-container{
                width: @a-hundred-percent;
                height: calc(@a-hundred-percent - @base-height*5);
                .ft-suggest-win-content-title{
                    width: @a-hundred-percent;
                    height: @a-hundred-percent*0.16;
                    text-align: center;
                    overflow: hidden;
                    .alert-warning{
                        font-size: 14px;
                        font-weight: bold;
                        color: #ff9500;
                        span{
                            font-size: @base-height*3;
                        }
                    }
                }
                .input-group{
                    width: @a-hundred-percent*0.8;
                    height: @a-hundred-percent*0.26;
                    margin-left: @a-hundred-percent*0.1;
                    padding-bottom: @a-hundred-percent*0.05;
                    .input-group-addon{
                        background: @auto-background-color;
                    }
                    .form-control{
                        height: @a-hundred-percent;
                        border:1px solid #ccc;
                        box-shadow:none;
                        font-size: 15px;
                    }
                    .form-control:focus{
                        border: 1px solid #9D9D9D;
                        box-shadow:0 0 5px #ccc;
                        background: #FFF8D7;
                    }
                }
                textarea{
                    width: @a-hundred-percent*0.8;
                    height: @a-hundred-percent*0.5;
                    margin-left: @a-hundred-percent*0.1;
                    border:1px solid #ccc;
                    box-shadow:none;
                    font-size: 15px;
                }
                textarea:focus{
                    border: 1px solid #9D9D9D;
                    box-shadow:0 0 5px #ccc;
                    background: #FFF8D7;
                }
            }
            .ft-suggest-win-btn{
                width: @a-hundred-percent;
                height: @base-height*5;
                line-height: @base-height*5;
                button{
                    box-shadow:none;
                    outline: none;
                    background: #fff;
                    font-size: 15px;
                }
                &:hover button{
                    background: #FFF8D7;
                }
            }
        }
    }
    .ft-suggest-mask{
        position: absolute;
        left: 0;
        top: 0;
        width: @a-hundred-percent;
        height: @a-hundred-percent;
        background:rgba(0, 0, 0, 0.1);
        z-index: 990;
    }
    .alert-danger{
        position: absolute;
        top:calc(50% - 40px);
        left: calc(50% - 150px);
        width: 300px;
        height: 80px;
        z-index: 1100;
    }
}
&:hover .icon-jianyi{
    color:@font-hover-color;
}

.win-rock{
    animation:winRock 0.4s linear 1;
    -webkit-animation:winRock 0.4s linear 1;
}

@keyframes winRock{
    10% {

        -webkit-transform: translateX(30px);

        }

    20% {

        -webkit-transform: translateX(0px);

        }
    30% {

        -webkit-transform: translateX(-30px);

        }
    40% {

        -webkit-transform: translateX(0px);

        }
    50% {

        -webkit-transform: translateX(30px);

        }
    60% {

        -webkit-transform: translateX(0px);

        }
    70% {

        -webkit-transform: translateX(-30px);

        }
    80% {

        -webkit-transform: translateX(0px);

        }
    90% {

        -webkit-transform: translateX(30px);

        }
    100% {

        -webkit-transform: translateX(0px);

        }
}
@-webkit-keyframes winRock{
    10% {

        -webkit-transform: translateX(30px);

        }

    20% {

        -webkit-transform: translateX(0px);

        }
    30% {

        -webkit-transform: translateX(-30px);

        }
    40% {

        -webkit-transform: translateX(0px);

        }
    50% {

        -webkit-transform: translateX(30px);

        }
    60% {

        -webkit-transform: translateX(0px);

        }
    70% {

        -webkit-transform: translateX(-30px);

        }
    80% {

        -webkit-transform: translateX(0px);

        }
    90% {

        -webkit-transform: translateX(30px);

        }
    100% {

        -webkit-transform: translateX(0px);

        }
}
.win-hide{
    position: absolute;
        left: @a-hundred-percent*0.5;
        top: -@a-hundred-percent;
        width: 0;
        height: 0;
        background: #fff;
        border-radius: 5px;
        z-index: -100;
        text-align: center;
        transition: all 0.4s;
        -ms-transition:all 0.4s;
        -moz-transition:all 0.4s;
        -o-transition:all 0.4s;
        -webkit-transition:all 0.4s;
}

</style>



