<template>
    <div class="ft-httpTest-container">
        <div class="ft-httpTest-title">
            <div class="alert alert-warning alert-dismissible" role="alert" v-if="false">
            </div>
        </div>
        <div class="ft-httpTest-addr">
            <div class="btn-group">
                <button class="btn btn-default btn-lg dropdown-toggle" type="button" 
                 data-toggle="dropdown" aria-haspopup="true" 
                aria-expanded="false">
                {{value}}
                <span class="caret"></span>
                </button>
                <ul class="dropdown-menu">
                    <li v-for="option in options" @click="selectOption(option.value)">
                        <a href="#">{{option.value}}</a>
                    </li>
                </ul>
            </div>
            <div class="input-group input-group-lg">
                <input type="text" class="form-control" placeholder="http://" aria-describedby="sizing-addon1" v-model="url">
                <span class="input-group-btn">
                    <button class="btn btn-default" type="button" @click="execute">
                        <i class="icon iconfont icon-enter"></i> 
                    </button>
                </span>
            </div>
        </div>
        <div class="ft-httpTest-req">
            <div class="ft-httpTest-req-title">
                Request
            </div>
            <div class="ft-httpTest-req-container">
                <ul class="nav nav-tabs">
                    <li role="presentation" :class="active=='body'?'active':''" @click="navActive('body')"><a href="#">Body</a></li>
                    <li role="presentation" :class="active=='head'?'active':''"  @click="navActive('head')"><a href="#">Head</a></li>
                    <li role="presentation" class="dropdown">
                        <a class="dropdown-toggle" data-toggle="dropdown" href="#" role="button" aria-haspopup="true" aria-expanded="true">
                        Content-Type <span class="caret"></span>
                        </a>
                        <ul class="dropdown-menu">
                            <li v-for="type in contentTypes" @click="selecType(type.value)">
                                <a href="#">{{type.value}}</a>
                            </li>
                        </ul>
                    </li>
                </ul>
                <textarea v-show="active=='body'?true:false" class="form-control" v-model="reqBody" placeholder="key1: value1 
key2: value2 
"></textarea>
                <textarea v-show="active=='head'?true:false" class="form-control" v-model="contentType">{{contentType}}</textarea>
            </div>
        </div>
        <div class="ft-httpTest-resp">
            <div class="ft-httpTest-resp-title">
                Response
            </div>
            <div class="ft-httpTest-resp-container">
                <ul class="nav nav-tabs">
                    <li role="presentation" :class="respActive=='body'?'active':''" @click="navRespActive('body')"><a href="#">Body</a></li>
                    <li role="presentation" :class="respActive=='head'?'active':''"  @click="navRespActive('head')"><a href="#">Head</a></li>
                </ul>
                <textarea v-show="respActive=='body'?true:false" class="form-control" readonly>{{respBody}}</textarea>
                <textarea v-show="respActive=='head'?true:false" class="form-control" readonly>{{respHead}}</textarea>    
            </div>
        </div>
        <div class="alert alert-danger alert-dismissible" role="alert" v-if="showTitle?true:false">
            <a href="#" class="close"  @click="hideTitle">
                &times;
            </a>
                <strong>提示：</strong>
                {{msg}}
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            options:[{
                key:'1',
                value:'POST'
            },{
                key:'2',
                value:'GET'
            },{
                key:'3',
                value:'PUT'
            },{
                key:'4',
                value:'PATCH'
            },{
                key:'5',
                value:'DELETE'
            }],
            contentTypes:[{
                key:'1',
                value:'application/xml'
            },{
                key:'2',
                value:'application/json'
            },{
                key:'3',
                value:'multipart/form-data'
            },{
                key:'4',
                value:'application/x-www-form-urlencode'
            }],
            value:'POST',
            show:false,
            active:'body',
            contentType:'Content-Type:application/json',
            respActive:'body',
            reqBody:'',
            respBody:'',
            respHead:'',
            url:'',
            showTitle:false,
            msg:''
        }
    },
    methods:{
        selectOption:function(value){
            this.value=value;
        },
        showOptions:function(){
            this.show=true;
        },
        hideOptions:function(){
            this.show=false;
        },
        navActive:function(val){
            this.active=val;
        },
        selecType:function(val){
            this.contentType="Content-Type:"+val;
            this.active="head";
        },
        navRespActive:function(val){
            this.respActive=val;
        },
        hideTitle:function(){
            this.msg='';
            this.showTitle=false;
        },
        execute:function(){
             /**请求后台 */
            var that=this;
            that.respBody='';
            that.respHead='';
            that.msg='';
            if(that.url==''){
                that.msg="请输入http测试地址！";
                that.showTitle=true;
            }else{ 
                that.hideTitle();
                var params =  this.$Qs.stringify({
                    url:that.url,
                    body:that.reqBody,
                    contentType:that.contentType.replace("Content-Type:","")
                });
                this.$Axios.post('/HttpService/'+that.value.toLowerCase(),params).then((res)=>{
                    var data=res.data;
                    if(!!data.success){
                        that.respBody=data.body;
                        that.respHead=data.head;
                        that.msg=data.errMsg;
                        that.showTitle=true;
                    }else{
                        that.msg=data.errMsg;
                        that.showTitle=true;
                    }
                });
            }
            
        }
    }
}
</script>
<style lang="less" >
@a-hundred-percent:100%;
@selections-width:120px;
@selections-height:50px;
@head-height:0px;
@base-height:10px;

.ft-httpTest-container{
    height: calc(@a-hundred-percent - @head-height);
    width: @a-hundred-percent;
    text-align: center;
    font-size: 20px;
    .ft-httpTest-title{
        width: @a-hundred-percent;
        height: calc((@a-hundred-percent - @head-height/4)*0.1);
        overflow: hidden;
        .alert-warning{
            font-size: 16px;
            font-weight: bold;
            color: #ff9500;
            span{
                font-size: @base-height*3;
            }
        }
    }
    .ft-httpTest-addr{
        width: @a-hundred-percent*0.6;
        height: calc((@a-hundred-percent - @head-height/4)*0.1);
        margin-left: @a-hundred-percent*0.2;
        text-align: center;
        .btn-group{
            float: left;
            width: @selections-width;
            .btn-default{
                background:#fff;
                background-color:#fff;
            }
            button {
                width: @selections-width;
                border-radius: 0;
                border-right: 0;
            }
            &:hover button{
                background: #FFF8D7;
            }
            ul{
                li{
                    height: @selections-height*0.6;
                    line-height: @selections-height*0.6;
                    a{
                        height: @selections-height*0.6;
                        line-height: @selections-height*0.6;
                        font-size: 20px;
                        font-family: 'Courier New', Courier, monospace;
                    }
                    &:hover a{
                        background: #FFF8D7;
                    }
                }
                
            }
        }
        .input-group{
            float: left;
            width: calc(@a-hundred-percent - @selections-width);
            overflow: hidden;
            input{
                border-radius:0;
                border: 1px solid #ccc;
                box-shadow:none;
            }
            input:focus{
                border: 1px solid #9D9D9D;
                box-shadow:0 0 5px #ccc;
                background: #FFF8D7;
            }
            span{
                button{
                    border-radius:0;
                    //border: 1px solid #ccc;
                    box-shadow:none;
                    outline: none;
                    background: #fff;
                    .icon{
                        font-size: 26px;
                        text-align: center;
                        line-height: 23px;
                        color: rgba(0, 0, 0, 0.6);
                    }
                }
                &:hover button{
                    background: #FFF8D7;
                }
                button:focus .icon-enter{
                    color:rgba(0, 0, 0, 0.8);
                } 
            }
        }     
    }
    .ft-httpTest-req{
        width: @a-hundred-percent;
        height: calc((@a-hundred-percent - @head-height/4)*0.4);
        .ft-httpTest-req-title{
            font-size: 20px;
            font-weight: bolder;
            width: @a-hundred-percent*0.6;
            height: @base-height*4;
            line-height: @base-height*4;
            margin-left: @a-hundred-percent*0.2;
            text-align: left;
        }
        .ft-httpTest-req-container{
            width: @a-hundred-percent*0.6;
            height: calc(@a-hundred-percent - @base-height*4);
            margin-left: @a-hundred-percent*0.2;
            text-align: left;
            ul{
                width: @a-hundred-percent;
                height: @base-height*4.4;
                li{
                    a{
                        font-size: 16px;
                        color: #000;
                        background: #fff;
                    }
                    &:hover a{
                        //background: #FFF8D7;
                        //background-color: #FFF8D7;
                        border-color: #9D9D9D;
                    }
                    .dropdown-menu{
                        width: auto;
                        height: auto;
                        background: #fff; 
                        li{
                            
                            a{
                                background: #fff;
                            }
                            &:hover a{
                                background: #FFF8D7;
                            }
                        }
                        
                    } 
                }
                .open>a{
                    border-color: #ccc;
                }
            }
            .form-control{
                height: calc(@a-hundred-percent - @base-height*4.4 );
                border:1px solid #ccc;
                box-shadow:none;
                font-size: 16px;
            }
            .form-control:focus{
                border: 1px solid #9D9D9D;
                box-shadow:0 0 5px #ccc;
                background: #FFF8D7;
            }
        }
    }
    .ft-httpTest-resp{
        width: @a-hundred-percent;
        height: calc((@a-hundred-percent - @head-height/4)*0.4);
        .ft-httpTest-resp-title{
            font-size: 20px;
            font-weight: bolder;
            width: @a-hundred-percent*0.6;
            height: @base-height*4;
            line-height: @base-height*4;
            margin-left: @a-hundred-percent*0.2;
            text-align: left;
        }
        .ft-httpTest-resp-container{
            width: @a-hundred-percent*0.6;
            height: calc(@a-hundred-percent - @base-height*4);
            margin-left: @a-hundred-percent*0.2;
            text-align: left;
            ul{
                width: @a-hundred-percent;
                height: @base-height*4.4;
                li{
                    a{
                        font-size: 16px;
                        color: #000;
                        background: #fff;
                    }
                    &:hover a{
                        //background: #FFF8D7;
                        //background-color: #FFF8D7;
                        border-color: #9D9D9D;
                    }
                    .dropdown-menu{
                        width: auto;
                        height: auto;
                        background: #fff; 
                        li{
                            
                            a{
                                background: #fff;
                            }
                            &:hover a{
                                background: #FFF8D7;
                            }
                        }
                        
                    } 
                }
            }
            .form-control{
                height: calc(@a-hundred-percent - @base-height*5.4 );
                border:1px solid #ccc;
                box-shadow:none;
                font-size: 16px;
                background: #FFF8D7;
            }
        }
    }
    .alert-danger{
        position: absolute;
        top:calc(50% - 40px);
        left: calc(50% - 150px);
        width: 300px;
        height: 80px;
        z-index: 200;
    }
}
</style>



