<template>
    <div id="ft-fileCompare">
        <SimpleHead></SimpleHead>
        <div class="ft-fileCompare-container">
            <iframe src="/mergely/mergely.html" style="width:100%;height:100%" frameborder="0"></iframe>
        </div>
        <Suggest ref="suggest"></Suggest>
    </div>
</template>

<script>
import SimpleHead from '@/components/SimpleHead';
import Suggest from '@/components/head/Suggest';

export default {
    data(){
        return{
            }
    },
    components:{
        SimpleHead:SimpleHead,
        Suggest:Suggest
    },
    mounted:function(){
    }
    
}
</script>
<style lang="less" scoped>
@a-hundred-percent:100%;
@head-height:16px;
@base-height:10px;

#ft-fileCompare{
    width: @a-hundred-percent;
    height: @a-hundred-percent;
    .ft-fileCompare-container{
        width: @a-hundred-percent;
        height: calc(@a-hundred-percent - @head-height - @base-height);
        padding-top: @head-height;
    }
}
</style>

    


