import Vue from 'vue'
import Router from 'vue-router'
import Http from '@/components/Http'
import JsonFormat from '@/components/format/JsonFormat'
import XmlFormat from '@/components/format/XmlFormat'
import FileCompare from '@/components/compare/FileCompare'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Http',
      component: Http
    },{
      path: '/json',
      name: 'JsonFormat',
      component: JsonFormat
    },{
      path: '/xml',
      name: 'XmlFormat',
      component: XmlFormat
    },{
      path: '/compare',
      name: 'FileCompare',
      component: FileCompare
    }
  ]
})



